<template>
  <div class="collect h100">
    <a-layout class="bt h100">
      <slider-menu :defaultModule="selectRoute"></slider-menu>
      <a-layout-content>
        <router-view />
      </a-layout-content>
    </a-layout>
  </div>
</template>
<script>
export default {
  name: "collect",
  data() {
    return {
      selectRoute: ""
    };
  },
  watch: {
    // 监听路由变化
    $route: {
      handler: function() {
        this.getSelectRoute();
      },
      // 深度观察监听
      deep: true
    }
  },
  methods: {
    getSelectRoute() {
      let thirdModule = this.$fun.getThirdRoute(this);
      if (this.$global.displaySliderSecondMenu.includes(thirdModule)) {
        this.selectRoute = this.$fun.getLastRoute(this);
      } else {
        this.selectRoute = thirdModule;
      }
    }
  },
  created() {
    this.getSelectRoute();
  }
};
</script>
<style>
.collect {
  height: 100%;
}
</style>